import React, {Component} from 'react';
import {
    Card,
    CardImg,
    CardImgOverlay,
    CardText,
    CardBody,
    CardTitle,
    Breadcrumb,
    BreadcrumbItem,
    Form, FormGroup, Label, Col, Input, Button
} from 'reactstrap';
import { Link } from 'react-router-dom';
import 'jquery';
import 'jquery.soap';
import {IP} from "../../shared/website";

function RenderMenuItem({book}) {
    return (

        <Card>
            <Link to={`/menu/${book.id}`}>
                <CardImg width="100%" object src={book.image} alt={book.name}/>
                <CardTitle>{book.name}</CardTitle>
            </Link>
            <CardTitle>{book.price}</CardTitle>
        </Card>
    );
}

function checkSameID(book, id) {
    const book_id = book.id;
    if (book_id === id) return true;
    else return false;
}


class Menu extends Component {
    constructor(props) {
        super(props);
        this.state = {
            totalBooks: props.books,
            currentBooks: props.books,
            data:[],
            pageNumber:1,
            totalPage:null,
            searchBooks: [],
        };
        this.handleInputChange = this.handleInputChange.bind(this);
        this.getBooks = this.getBooks.bind(this);
        this.handleClickForNextPage = this.handleClickForNextPage.bind(this);
        this.handleClickForLastPage = this.handleClickForLastPage.bind(this);
        this.getPostData = this.getPostData.bind(this);
        this.getBooks(this.state.pageNumber).then(r => null);
    }

    async getBooks(pageNumber) {
        fetch('http://' + IP +'/book/page/' + pageNumber,{
            method:'GET'
        })
            .then(res =>res.json())
            .then((data) => {
                // console.log("data:", data);
                let bookList = data.data;
                if (bookList === undefined) {
                    alert(""); return;
                }
                this.setState({currentBooks:bookList});
                this.setState({totalBooks:bookList});
                this.setState({pageNumber:pageNumber});
                this.setState({totalPage:data.totalPage});
            });
    }

    getPostData(name) {
        let soap= "<soapenv:Envelope xmlns:soapenv=\"http://schemas.xmlsoap.org/soap/envelope/\" xmlns:web=\"http://webservice.bookstore.com/\">\n" +
            "   <soapenv:Header/>\n" +
            "   <soapenv:Body>\n" +
            "      <web:findBookByDescription>\n" +
            "         <!--Optional:-->\n" +
            "         <description>" + name + "</description>\n" +
            "      </web:findBookByDescription>\n" +
            "   </soapenv:Body>\n" +
            "</soapenv:Envelope>"
        return soap;
    }

    handleInputChange(event) {
        const target = event.target;
        const value = target.value;
        if (value === "") {
            this.setState({
                searchBooks: [],
            });
            return;
        }
        let $ = require('jquery');
        require('jquery.soap');
        $.ajax({
            url: 'http://' + IP +'/ws/searchByDescription',
            type: "POST",
            dataType:"text",
            contentType:"text/xml;charset=UTF-8",
            data: this.getPostData(value),
            success: function (data) {
                data = data.toString();
                let pattern=/<return>(.*)<\/return>/i
                let code= pattern.exec(data);
                let json = code[1];
                let books = JSON.parse(json);
                console.log("data = ", books);
                this.setState({
                    searchBooks: books,
                });
            }.bind(this)
        });

    }

    handleClickForNextPage(event) {
        if (this.state.totalPage !== null) {
            if (this.state.pageNumber + 1 > this.state.totalPage) {
                return;
            }
        }
        this.getBooks(this.state.pageNumber + 1).then(r => null);
    }

    handleClickForLastPage(event) {
        if (this.state.pageNumber - 1 <= 0) {
            return;
        }
        this.getBooks(this.state.pageNumber - 1).then(r => null);
    }

    render() {
        let menu = this.state.currentBooks.map((book) =>{
            return (
                <div key = {book.id} className="col-12 col-md-2 m-1">
                    <RenderMenuItem book={book} />
                </div>
            )
        });
        console.log("menu = ", menu);
        let searchResults = [];
        searchResults = this.state.searchBooks.map((book) => {
            console.log("book = ", book);
            return (
                <div key = {book.id} className="col-12 col-md-2 m-1">
                    <RenderMenuItem book={book} />
                </div>
            )
        });
        console.log("search reuslt = ", searchResults);
        if (searchResults.length !== 0) {
            menu = searchResults;
        }
        console.log("menu = ", menu);



        return (
            <div className="container">
                <div className="row">
                    <Breadcrumb>
                        <BreadcrumbItem><Link to='home'></Link></BreadcrumbItem>
                        <BreadcrumbItem active></BreadcrumbItem>
                    </Breadcrumb>
                    <div className="col-12">
                        <h3></h3>
                        <hr />
                    </div>
                </div>
                <div className="row">
                    <Form >
                        <FormGroup row>
                            <Label htmlFor="bookName" md={4}></Label>
                            <Col md={8}>
                                <Input type="text" id="bookName" name="bookName"
                                       placeholder=""
                                       value={this.state.account}
                                       onChange={this.handleInputChange} />
                            </Col>
                        </FormGroup>
                    </Form>

                </div>
                <div className="row">
                    {menu}
                    <Button type="submit" color="primary" onClick={this.handleClickForLastPage}></Button>
                    <Button type="submit" color="danger" onClick={this.handleClickForNextPage}></Button>
                    <div>{this.state.pageNumber}</div>
                    <div>{this.state.totalPage}</div>
                </div>
            </div>
        );
    }
}


export default Menu;
