import React, { Component } from "react";
import {Card, CardBody, CardImg, CardText, CardTitle, Media} from "reactstrap";
import {Link, Redirect, withRouter} from "react-router-dom";
import Order from "./OrderComponent"
import {IP} from "../../shared/website";


class RenderBookInCar extends Component {
    constructor(props) {
        super(props);
        this.state = {
            cart: props.cart
        }
        console.log("in the cart", props.cart);
        this.handleSubmit = this.handleSubmit.bind(this);
        this.inCart = this.inCart.bind(this);
    }

    handleSubmit(event) {
        event.preventDefault();
        alert("");
        this.props.history.push("/order");
        fetch('http://' + IP + '/purchase/' + sessionStorage.getItem("user_id"),{
            method:'GET',
            mode: 'cors',
            credentials: 'include'
        })
            .then(res =>res.json())
            .then((data) => {
                console.log(data);
                if (data === undefined || data === null) {
                    alert("")
                }
                if (data.info.result === "success") {
                    if (data.info.fail_books === 0) {
                        alert("");
                    }

                }
                if (data.info.result === "fail") {
                    alert(data.info.message);
                }
            })
    }


    inCart(book, cart) {
        let len = cart.length;
        for (let i = 0; i < len; i++) {
            if (book.id === cart[i].id) return true;
        }
        return false;
    }
    render() {
        let cart = this.props.cart;
        let book_filtered = this.props.books.filter((book) => this.inCart(book, cart));
        let renderedBooks = book_filtered.map((book) => {
            let len = cart.length;
            let cart_item;
            for (let i = 0; i < len; i++) {
                if (book.id === cart[i].id) cart_item = cart[i];
            }
            return (
                <div key = "bookimage" className="col-12 col-md-12">
                    <Card>
                        <Media>
                            <div className="col-12 col-md-2">
                                <CardImg width="100%" object src={book.image} alt={book.name}/>
                            </div>
                            <div className="col-12 col-md-7 m-2">
                                <Media body>
                                    <Media heading>{book.name}</Media>
                                    <Media><Media heading>{book.price * cart_item.number}</Media></Media>
                                    <Media><Media heading>:{cart_item.number}</Media></Media>
                                </Media>
                            </div>
                        </Media>
                    </Card>
                </div>
            );
        });
        if (cart === undefined || cart.length === 0) return (
            <div key = "bookimage" className="col-12 col-md-12">
                <Card>
                    <h1></h1>
                </Card>
            </div>
        );
        else return (
            <React.Fragment>
                {renderedBooks}
                <div key = "bookdescription" className="col-12 col-md-2 m-1">
                    <Link to={`/cart/`} onClick={this.handleSubmit}>
                        <CardImg  object src="/assets/images/buy.png" alt="" />
                    </Link>
                </div>

            </React.Fragment>
        );
    }


}
export default withRouter(RenderBookInCar);


