import React, { Component } from "react";
import {
    Card,
    CardImg,
    CardText,
    CardBody,
    CardTitle,
    CardSubtitle,
    Breadcrumb,
    BreadcrumbItem,
    Form,
    FormGroup, Label, Col, Input, Button
} from "reactstrap";
import {Link, Redirect, withRouter} from "react-router-dom";
import {IP} from "../../shared/website";

class Login extends Component {
    constructor(props) {
        super(props);
        this.state={
            account:'',
            password:'',
            logined:false,
            logouted:false
        }
        this.handleSubmit = this.handleSubmit.bind(this);
        this.handleInputChange = this.handleInputChange.bind(this);
    }


    handleInputChange(event) {
        const target = event.target;
        const value = target.type === 'checkbox' ? target.check : target.value;
        // console.log(value);
        const name = target.name;
        this.setState({
            [name]: value
        });
    }

    handleSubmit(event) {
        event.preventDefault();
        if (this.state.account === "") {
            alert(""); return;
        }
        else if (this.state.password === "") {
            alert(""); return;
        }
        let formData = new URLSearchParams();
        formData.append('account', this.state.account);
        formData.append('password', this.state.password);
        console.log(formData);
        fetch('http://' + IP + '/user/login',{
            method:'POST',
            credentials: 'include',
            body:formData
        })
            .then(res =>res.json())
            .then((data) => {
                console.log("data:", data);
                if (data === undefined) {
                    alert(""); return;
                }
                if (data.info === undefined) {
                    alert(""); return;
                }
                if (data.info.result === 'success') {
                    if (data.info.user.isBlocked === 1) {
                        alert("!");
                        return;
                    }
                    sessionStorage.setItem("username", this.state.account);
                    sessionStorage.setItem("user_id", data.info.user.userid);
                    if (data.info.user.usertype === 2) sessionStorage.setItem("isAdmin", "true");
                    else sessionStorage.removeItem("isAdmin");
                    alert("");
                    this.props.history.push("/home");
                }
                else {
                    alert("");
                }
            })

    }
    render() {
        if (this.props.logined && this.state.logouted === false) {
            fetch('http://' + IP + '/user/logout',{
                method:'GET',
                credentials: 'include'
            })
                .then(res =>res.json())
                .then((data) => {
                    sessionStorage.removeItem("username");
                    this.setState({"logouted":true});
                });
            return (
                <div></div>
            )
        }
        else {
            return (
                <div className="container">
                    <div className="row row-content">
                        <div className="col-12">
                            <h3></h3>
                        </div>
                        <div className="col-12 col-md-9">
                            <Form onSubmit={this.handleSubmit}>
                                <FormGroup row>
                                    <Label htmlFor="account" md={2}></Label>
                                    <Col md={10}>
                                        <Input type="text" id="account" name="account"
                                               placeholder=""
                                               value={this.state.account}
                                               onChange={this.handleInputChange} />
                                    </Col>
                                </FormGroup>
                                <FormGroup row>
                                    <Label htmlFor="password" md={2}></Label>
                                    <Col md={10}>
                                        <Input type="password" id="password" name="password"
                                               placeholder=""
                                               value={this.state.password}
                                               onChange={this.handleInputChange} />
                                    </Col>
                                </FormGroup>
                                <FormGroup row>
                                    <Col md={{size:10, offset:2}}>
                                        <Button type="submit" color="primary">
                                            
                                        </Button>
                                    </Col>
                                </FormGroup>
                            </Form>
                        </div>
                    </div>
                </div>
            );
        }

    }
}

export default withRouter(Login);
