import React, { Component } from "react";
import {
    Card,
    CardImg,
    CardText,
    CardBody,
    CardTitle,
    CardSubtitle,
    Breadcrumb,
    BreadcrumbItem,
    Form,
    FormGroup, Label, Col, Input, Button
} from "reactstrap";
import {Link} from "react-router-dom";
import request from "../utils/helper";
import {IP} from "../../shared/website";

class Signup extends Component {
    constructor(props) {
        super(props);
        this.state={
            account:'',
            password:'',
            repeatpassword:'',
            telnum:'',
            email:'',
            agree: false,
        }
        this.handleSubmit = this.handleSubmit.bind(this);
        this.handleInputChange = this.handleInputChange.bind(this);
    }


    handleInputChange(event) {
        const target = event.target;
        const value = target.type === 'checkbox' ? target.checked : target.value;
        const name = target.name;
        this.setState({
            [name]: value
        });
    }

    handleSubmit(event) {
        event.preventDefault();
        if (this.state.agree === false) {
            alert("");
            return;
        }
        console.log(this.state);
        if (this.state.agree === undefined || this.state.account === undefined || this.state.password === undefined
            || this.state.repeatpassword === undefined || this.state.telnum === undefined || this.state.email === undefined) {
            return;
        }

        if (this.state.account.length < 6 || this.state.account.length > 15) {
            alert("615");
            return;
        }
        if (this.state.password.length < 6 || this.state.password.length > 15) {
            alert("615");
            return;
        }

        if (this.state.password !== this.state.repeatpassword) {
            alert("");
            return;
        }
        if (this.state.telnum.length !== 11) {
            alert("");
            return;
        }
        if(!(/^[a-zA-Z0-9.!#$%&'*+\/=?^_`{|}~-]+@[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,61}[a-zA-Z0-9])?(?:\.[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,61}[a-zA-Z0-9])?)*$/.test(this.state.email))) {
            alert('Email');
            return;
        }

        let localstate = this.state;
        let formData = new URLSearchParams();

        formData.append('account', this.state.account);
        formData.append('password', this.state.password);
        formData.append('telnum', this.state.telnum);
        formData.append('email', this.state.email);
        // console.log(formData);
        fetch('http://' + IP + '/user/signup',{
            method:'POST',
            body:formData
        })
            .then(res =>res.json())
            .then((data) => {
                console.log(data);
                if (data === undefined || data === null) {
                    alert("!");
                    return;
                }
                alert(data.info.message);
                });

    }
    render() {
        return (
            <div className="container">
                <div className="row row-content">
                    <div className="col-12">
                        <h3></h3>
                    </div>
                    <div className="col-12 col-md-9">
                        <Form onSubmit={this.handleSubmit}>
                            <FormGroup row>
                                <Label htmlFor="account" md={2}></Label>
                                <Col md={10}>
                                    <Input type="text" id="account" name="account"
                                           placeholder=""
                                           value={this.state.account}
                                           onChange={this.handleInputChange} />
                                </Col>
                            </FormGroup>
                            <FormGroup row>
                                <Label htmlFor="password" md={2}></Label>
                                <Col md={10}>
                                    <Input type="password" id="password" name="password"
                                           placeholder=""
                                           value={this.state.password}
                                           onChange={this.handleInputChange} />
                                </Col>
                            </FormGroup>
                            <FormGroup row>
                                <Label htmlFor="repeatpassword" md={2}></Label>
                                <Col md={10}>
                                    <Input type="password" id="repeatpassword" name="repeatpassword"
                                           placeholder=""
                                           value={this.state.repeatpassword}
                                           onChange={this.handleInputChange} />
                                </Col>
                            </FormGroup>
                            <FormGroup row>
                                <Label htmlFor="telnum" md={2}></Label>
                                <Col md={10}>
                                    <Input type="tel" id="telnum" name="telnum"
                                           placeholder=""
                                           value={this.state.telnum}
                                           onChange={this.handleInputChange} />
                                </Col>
                            </FormGroup>
                            <FormGroup row>
                                <Label htmlFor="email" md={2}></Label>
                                <Col md={10}>
                                    <Input type="email" id="email" name="email"
                                           placeholder=""
                                           value={this.state.email}
                                           onChange={this.handleInputChange} />
                                </Col>
                            </FormGroup>
                            <FormGroup row>
                                <Col md={{size:6, offset:2}}>
                                    <FormGroup check>
                                        <Label check>
                                            <Input type="checkbox" name="agree"
                                                   checked={this.state.agree}
                                                   onChange={this.handleInputChange} /> {''}
                                            <strong></strong>
                                        </Label>
                                    </FormGroup>
                                </Col>
                            </FormGroup>
                            <FormGroup row>
                                <Col md={{size:10, offset:2}}>
                                    <Button type="submit" color="primary">
                                        
                                    </Button>
                                </Col>
                            </FormGroup>
                        </Form>
                    </div>
                </div>
            </div>
        );
    }
}

export default Signup;
