import React, {Component} from 'react'
import {Jumbotron, Navbar, Nav, NavbarToggler, Collapse, NavItem, NavbarBrand} from 'reactstrap'
import { NavLink } from "react-router-dom";

class Header extends Component {
    constructor(props) {
        super(props);
        this.state = {
            isNavOpen: false
        };
        this.toggleNav = this.toggleNav.bind(this);
    }

    toggleNav() {
        this.setState({
                isNavOpen: !this.state.isNavOpen
            }
        )
    }

    render() {
        let isAuthenticated =  !!sessionStorage.getItem("username");
        let isAdmin = !!sessionStorage.getItem("isAdmin");
        if (!isAuthenticated) {
            return (
                <React.Fragment>
                    <Navbar dark expand="md">
                        <div className="container">
                            <NavbarToggler onClick={this.toggleNav} />
                            <NavbarBrand className="mr-auto" href="/">
                                <img src="assets/images/logo.png" height="30" width="41"
                                     alt="" />
                            </NavbarBrand>
                            <Collapse isOpen={this.state.isNavOpen} navbar>
                                <Nav navbar>
                                    <NavItem>
                                        <NavLink className="nav-link" to="/user/login">
                                            <span className="fa fa-user-plus fa-lg"></span> 
                                        </NavLink>
                                    </NavItem>
                                    <NavItem>
                                        <NavLink className="nav-link" to="/user/signup">
                                            <span className="fa fa-user-plus fa-lg"></span> 
                                        </NavLink>
                                    </NavItem>
                                </Nav>
                            </Collapse>
                        </div>
                    </Navbar>
                    <Jumbotron>
                        <div className="container">
                            <div className="row row-header">
                                <div className="col-12 col-sm-6">
                                    <h1>2200</h1>
                                    <p>2200</p>
                                </div>
                            </div>
                        </div>
                    </Jumbotron>
                </React.Fragment>
            );
        }
        else if (isAuthenticated && !isAdmin){
            return (
                <React.Fragment>
                    <Navbar dark expand="md">
                        <div className="container">
                            <NavbarToggler onClick={this.toggleNav} />
                            <NavbarBrand className="mr-auto" href="/">
                                <img src="assets/images/logo.png" height="30" width="41"
                                     alt="" />
                            </NavbarBrand>
                            <Collapse isOpen={this.state.isNavOpen} navbar>
                                <Nav navbar>
                                    <NavItem>
                                        <NavLink className="nav-link" to="/home">
                                            <span className="fa fa-home fa-lg"></span> 
                                        </NavLink>
                                    </NavItem>
                                    <NavItem>
                                        <NavLink className="nav-link" to="/menu">
                                            <span className="fa fa-list fa-lg"></span> 
                                        </NavLink>
                                    </NavItem>
                                    <NavItem>
                                        <NavLink className="nav-link" to="/cart">
                                            <span className="fa fa-shopping-cart fa-lg"></span> 
                                        </NavLink>
                                    </NavItem>
                                    <NavItem>
                                        <NavLink className="nav-link" to="/order">
                                            <span className="fa fa-recorder fa-lg"></span> 
                                        </NavLink>
                                    </NavItem>
                                    <NavItem>
                                        <NavLink className="nav-link" to="/aboutus">
                                            <span className="fa fa-info fa-lg"></span> 
                                        </NavLink>
                                    </NavItem>
                                    <NavItem>
                                        <NavLink className="nav-link" to="/contactus">
                                            <span className="fa fa-address-card fa-lg"></span> 
                                        </NavLink>
                                    </NavItem>
                                    <NavItem>
                                        <NavLink className="nav-link" to="/chater">
                                            <span className="fa fa-address-card fa-lg"></span> 
                                        </NavLink>
                                    </NavItem>
                                    <NavItem>
                                        <NavLink className="nav-link" to="/user/login">
                                            <span className="fa fa-user-plus fa-lg"></span> 
                                        </NavLink>
                                    </NavItem>
                                </Nav>
                            </Collapse>
                        </div>
                    </Navbar>
                    <Jumbotron>
                        <div className="container">
                            <div className="row row-header">
                                <div className="col-12 col-sm-6">
                                    <h1>: {sessionStorage.getItem("username")}</h1>
                                    <p>2200</p>
                                </div>
                            </div>
                        </div>
                    </Jumbotron>
                </React.Fragment>
            );
        }
        else if (isAuthenticated && isAdmin){
            return (
            <React.Fragment>
                <Navbar dark expand="md">
                    <div className="container">
                        <NavbarToggler onClick={this.toggleNav} />
                        <NavbarBrand className="mr-auto" href="/">
                            <img src="assets/images/logo.png" height="30" width="41"
                                 alt="" />
                        </NavbarBrand>
                        <Collapse isOpen={this.state.isNavOpen} navbar>
                            <Nav navbar>
                                <NavItem>
                                    <NavLink className="nav-link" to="/admin/menu">
                                        <span className="fa fa-address-card fa-lg"></span> 
                                    </NavLink>
                                </NavItem>
                                <NavItem>
                                    <NavLink className="nav-link" to="/admin/addbook">
                                        <span className="fa fa-address-card fa-lg"></span> 
                                    </NavLink>
                                </NavItem>
                                <NavItem>
                                    <NavLink className="nav-link" to="/admin/query">
                                        <span className="fa fa-address-card fa-lg"></span> 
                                    </NavLink>
                                </NavItem>
                                <NavItem>
                                    <NavLink className="nav-link" to="/admin/user">
                                        <span className="fa fa-address-card fa-lg"></span> 
                                    </NavLink>
                                </NavItem>
                                <NavItem>
                                    <NavLink className="nav-link" to="/chater">
                                        <span className="fa fa-address-card fa-lg"></span> 
                                    </NavLink>
                                </NavItem>
                                <NavItem>
                                    <NavLink className="nav-link" to="/user/login">
                                        <span className="fa fa-user-plus fa-lg"></span> 
                                    </NavLink>
                                </NavItem>
                            </Nav>
                        </Collapse>
                    </div>
                </Navbar>
                <Jumbotron>
                    <div className="container">
                        <div className="row row-header">
                            <div className="col-12 col-sm-6">
                                <h1>: {sessionStorage.getItem("username")}</h1>
                                <p>2200</p>
                            </div>
                        </div>
                    </div>
                </Jumbotron>
            </React.Fragment>
        );
        }
    }
}

export default Header;
