import React, { Component } from "react";
import {
    Card,
    CardImg,
    CardText,
    CardBody,
    CardTitle,
    CardSubtitle,
    Breadcrumb,
    BreadcrumbItem,
    Form,
    FormGroup, Label, Col, Input, Button
} from "reactstrap";
import {Link} from "react-router-dom";
import {IP} from "../../shared/website"

class AdminAddBook extends Component {
    constructor(props) {
        super(props);
        this.state={
            name:'',
            author:'',
            image:'',
            price:'',
            inventory: "",
            isbn:'',
            description:'',
            type:'',
        }

        this.handleSubmit = this.handleSubmit.bind(this);
        this.handleInputChange = this.handleInputChange.bind(this);
    }


    handleInputChange(event) {
        const target = event.target;
        const value = target.value;
        const name = target.name;
        this.setState({
            [name]: value
        });
    }

    handleSubmit(event) {
        event.preventDefault();
        let book = this.state;
        if (book.name === "") {
            alert("");
            return;
        }
        if (book.author === "") {
            alert("");
            return;
        }
        if (book.image === "") {
            alert("");
            return;
        }
        if (book.price === "") {
            alert("");
            return;
        }
        if (book.inventory === "") {
            alert("");
            return;
        }
        if (book.isbn === "") {
            alert("ISBN");
            return;
        }
        if (book.description === "") {
            alert("");
            return;
        }
        if (book.type === "") {
            alert("");
            return;
        }
        console.log(book);
        let params = new URLSearchParams();
        params.append('name', book.name);
        params.append('author', book.author);
        params.append('image', book.image);
        params.append('price', book.price);
        params.append('inventory', book.inventory);
        params.append('isbn', book.isbn);
        params.append('description', book.description);
        params.append('type', book.type);

        fetch('http://' +  IP +'/book/add',{
            method:'POST',
            body:params
        })
            .then(res =>res.json())
            .then((data) => {
                console.log(data);
                alert("");
            });
    }
    render() {
        return (
            <div className="container">
                <div className="row row-content">
                    <div className="col-12">
                        <h3></h3>
                    </div>
                    <div className="col-12 col-md-9">
                        <Form onSubmit={this.handleSubmit}>
                            <FormGroup row>
                                <Label htmlFor="name" md={2}></Label>
                                <Col md={10}>
                                    <Input type="text" id="name" name="name"
                                           placeholder="name"
                                           value={this.state.name}
                                           onChange={this.handleInputChange} />
                                </Col>
                            </FormGroup>
                            <FormGroup row>
                                <Label htmlFor="name" md={2}></Label>
                                <Col md={10}>
                                    <Input type="text" id="author" name="author"
                                           placeholder="author"
                                           value={this.state.author}
                                           onChange={this.handleInputChange} />
                                </Col>
                            </FormGroup>
                            <FormGroup row>
                                <Label htmlFor="image" md={2}></Label>
                                <Col md={10}>
                                    <Input type="text" id="image" name="image"
                                           placeholder="image"
                                           value={this.state.image}
                                           onChange={this.handleInputChange} />
                                </Col>
                            </FormGroup>
                            <FormGroup row>
                                <Label htmlFor="price" md={2}></Label>
                                <Col md={10}>
                                    <Input type="text" id="price" name="price"
                                           placeholder="price"
                                           value={this.state.price}
                                           onChange={this.handleInputChange} />
                                </Col>
                            </FormGroup>
                            <FormGroup row>
                                <Label htmlFor="inventory" md={2}></Label>
                                <Col md={10}>
                                    <Input type="text" id="inventory" name="inventory"
                                           placeholder="inventory"
                                           value={this.state.inventory}
                                           onChange={this.handleInputChange} />
                                </Col>
                            </FormGroup>
                            <FormGroup row>
                                <Label htmlFor="ISBN" md={2}>ISBN</Label>
                                <Col md={10}>
                                    <Input type="text" id="isbn" name="isbn"
                                           placeholder="ISBN"
                                           value={this.state.isbn}
                                           onChange={this.handleInputChange} />
                                </Col>
                            </FormGroup>
                            <FormGroup row>
                                <Label htmlFor="description" md={2}></Label>
                                <Col md={10}>
                                    <Input type="text" id="description" name="description"
                                           placeholder="description"
                                           value={this.state.description}
                                           onChange={this.handleInputChange} />
                                </Col>
                            </FormGroup>
                            <FormGroup row>
                                <Label htmlFor="description" md={2}></Label>
                                <Col md={10}>
                                    <Input type="text" id="type" name="type"
                                           placeholder="type"
                                           value={this.state.type}
                                           onChange={this.handleInputChange} />
                                </Col>
                            </FormGroup>
                            <FormGroup row>
                                <Col md={{size:10, offset:2}}>
                                    <Button type="submit" color="primary">
                                        
                                    </Button>
                                </Col>
                            </FormGroup>
                        </Form>
                    </div>
                </div>
            </div>
        );
    }
}

export default AdminAddBook;
