import React, {Component} from "react";
import {Card, CardTitle} from "reactstrap";
import RenderBookInCar from "./CartComponent";
import {IP} from "../../shared/website";


class Cart extends Component{
    constructor(props) {
        super(props);
        this.state={
            cart:[],
        }
        this.getCart = this.getCart.bind(this);
        this.getCart();
    }

    getCart() {
        fetch('http://' + IP + '/cart/' + sessionStorage.getItem("user_id"),{
            method:'GET',
            mode: 'cors',
            credentials: 'include'
        })
            .then((res) => {
                return res.json();
            })
            .then((data) => {
                console.log(data);
                console.log(data.instance);
                let newList = [];
                for(let book_id in data.instance){
                    let book_count = data.instance[book_id]
                    let book_summary = {
                        id: parseInt(book_id),
                        number: book_count,
                    }
                    newList = [...newList, book_summary];
                    //console.log(book + " " + data.currentCar[book])
                }
                console.log("cart", newList);
                this.setState({cart: newList});
            })

    }


    render() {
        let totalPrice = 0;
        let totalBooks = 0;
        let cart = this.state.cart;
        for(let i = 0, len = cart.length; i < len; i++) {
            totalPrice += parseFloat(cart[i].price) * cart[i].number;
            totalBooks += cart[i].number;
        }
        totalPrice = totalPrice.toFixed(2);
        return (
            <React.Fragment>
                <div className="container">
                    <RenderBookInCar books = {this.props.books} cart = {this.state.cart}/>
                    <div key = "bookTotalPrice" className="col-12 col-md-12">
                        <Card>
                            <CardTitle>{totalBooks} {totalPrice} </CardTitle>
                        </Card>
                    </div>
                </div>
            </React.Fragment>
        );
    }


}

export default Cart;
