import React, {Component} from 'react';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';


class DenseTable extends Component {
    constructor(prop) {
        super(prop);
        this.createData = this.createData.bind(this);
    }


    createData(createtime, username, payment) {
        return { createtime, username, payment};
    }


    render() {
        let rows = [];
        if (this.props.data === undefined) {
            rows = [];
        }
        else {
            for (let i = 0; i < this.props.data.length; ++i) {
                let order = this.props.data[i];
                if (order.username !== undefined) rows.push(this.createData(order.createtime, order.username, order.payment));
                else if (order.book !== undefined) rows.push(this.createData(order.createtime, order.book, order.count));
            }
        }

        return (
            <TableContainer component={Paper}>
                <Table  size="small" aria-label="a dense table">
                    <TableHead>
                        <TableRow>
                            <TableCell align="right">{this.props.title1}</TableCell>
                            <TableCell align="right">{this.props.title2}</TableCell>
                            <TableCell align="right">{this.props.title3}</TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {rows.map((row) => {
                            if (row === undefined) return <div></div>
                            else {
                                return (
                                    <TableRow key={row.createtime}>
                                        <TableCell align="right">
                                            {row.createtime}
                                        </TableCell>
                                        <TableCell align="right">{row.username}</TableCell>
                                        <TableCell align="right">{row.payment}</TableCell>
                                    </TableRow>
                                );
                            }


                        })}
                    </TableBody>
                </Table>
            </TableContainer>
        );
    }

}

export default DenseTable;
