import React, {Component} from "react";
import WebSocket from '../websocket';
import {Button, Card} from "@material-ui/core";
import SockJS from "sockjs-client";
import Stomp from "stompjs";
import {CardBody, Col, Input} from "reactstrap";
import {IP} from "../../shared/website";

function RenderWords({words, users, connected}) {
    let comment_fun = words.map((comment) =>{
        return (
            <div>
                <Card>
                    {comment}
                </Card>
            </div>
        );
    });
    let word = connected === true? "":"";


    if (comment_fun.length === 0) {
        return (
            <div key = "comment" className="col-12 col-md-10 m-2">
                <h4> {word}</h4>
                <h4>{users}</h4>
                <ul className="list-unstyled">
                    
                </ul>
            </div>
        );
    }
    else {
        return (
            <div key = "comment" className="col-12 col-md-10 m-2">
                <h4> {word}</h4>
                <h4>{users}</h4>
                <ul className="list-unstyled">
                    {comment_fun}
                </ul>
            </div>
        );
    }
}

var stompClient;
class Chater extends Component{

    constructor(props) {
        super(props);
        let client = new SockJS('http://' + IP +'/WebSocketServer');
        stompClient = Stomp.over(client);
        let user_id = sessionStorage.getItem("user_id");
        let headers = {userid:user_id};

        const message_callback = (message) => {
            let destination = message.headers.destination;
            if (destination === '/topic/user') {
                this.setState({
                    users: message.body
                });
            }
            if (destination === '/topic/greetings') {
                let username = JSON.parse(message.body).username;
                let word = JSON.parse(message.body).word;
                this.setState({
                    collected_word: [...this.state.collected_word, username + " : " + word]
                });
            }
        }

        stompClient.connect(headers, function (frame) {

            stompClient.subscribe('/topic/user', message_callback, headers);
            stompClient.subscribe('/topic/greetings', message_callback, headers);
        });

        this.state = {
            collected_word : [],
            stompClient : stompClient,
            word : "",
        }
        this.handleClick = this.handleClick.bind(this);
        this.handleInputChange = this.handleInputChange.bind(this);
    }

    handleInputChange(event) {
        const target = event.target;
        const value = target.value;
        this.setState({
            word: value
        });
    }

    handleClick(event){
        const target = event.target;
        const id = target.id;
        const value = target.value;

        let username = sessionStorage.getItem("username");
        let user_id = sessionStorage.getItem("user_id");

        if (username !== undefined && username !== null) {
            this.state.stompClient.send("/app/top",
                {userid:user_id},
                JSON.stringify({
                    username:username,
                    word:this.state.word
                })
            );
        }

    }

    render() {
        return (
            <React.Fragment>

                <Input type="text" id="name" name="name"
                       placeholder=""
                       value={this.state.name}
                       onChange={this.handleInputChange} />
                <Button type="submit" color="primary" id="delete" onClick={this.handleClick}>
                    
                </Button>
                <RenderWords words={this.state.collected_word} users={this.state.users} connected={stompClient.connected}/>
            </React.Fragment>
        );
    }


}

export default Chater;
