import React, { Component } from "react";
import Menu from "./service/MenuComponents";
import '../App.css';
import { COMMENTS } from "../shared/comments";
import { LEADERS } from "../shared/leaders";
import { PROMOTIONS } from "../shared/promotions";
import {ACCOUNTS} from "../shared/accounts";
import BookDetail from "./user/BookDetailComponent";
import Contact from "./utils/ContactComponent";
import Header from "./utils/HeaderComponent";
import Footer from "./utils/FooterComponent";
import Home from "./HomeComponent";
import {Switch, Route, Redirect} from "react-router-dom";
import About from "./utils/AboutComponent";
import Signup from "./service/SignupComponent";
import Login from "./service/LoginComponent";
import Cart from "./user/Cart";
import AdminMenuComponents from "./admin/AdminMenuComponent";
import AdminMenu from "./admin/AdminMenuComponent";
import AdminBookDetail from "./admin/AdminBookDetailComponent";
import PrivateRoute from "./PrivateRouteComponent";
import AdminAddBook from "./admin/AdminAddBookComponent";
import Order from "./user/OrderComponent";
import EchartsTest from "./utils/EchartsTest";
import Demo from "./utils/EchartsTest";
import AdminQueryComponent from "./admin/AdminQueryComponent";
import AdminQuery from "./admin/AdminQueryComponent";
import AdminUserBrowser from "./admin/AdminUserBrowserComponent";
import Chater from "./user/WebsocketChaterComponent";
import {IP} from "../shared/website";

class Main extends Component {

    constructor(props) {
        super(props);
        this.state = {
            books: Array(),
            comments: COMMENTS,
            promotion: PROMOTIONS,
            leaders: LEADERS,
            currentBook: null,
            car: [],
            accounts: ACCOUNTS,
            onLoginAccount: null,
            orders: Array()
        };
        fetch('http://' + IP +'/books',{
            method:'GET',
            credentials: 'include',
        })
            .then(res =>res.json())
            .then((data) => {
                console.log(data)
                this.setState({books:data});
            })
        fetch('http://' + IP +'/order/' + String(sessionStorage.getItem("user_id")),{
            method:'GET',
            credentials: 'include',
        })
            .then(res =>res.json())
            .then((data) => {
                console.log(data)
                this.setState({orders:data});
            })
        this.clearCar = this.clearCar.bind(this);
    }

    clearCar() {
        this.setState({car: []});
    }

    render() {
        const HomePage = () => {
            return (
                <Home book={this.state.books.filter((book)=> book.featured)[0]}
                    promotion={this.state.promotion.filter((promo) => promo.featured)[0]}
                      leader={this.state.leaders.filter((leader) => leader.featured)[0]}
                />
            )
        }

        const BookWithId = ({match}) =>{
            return (
                <BookDetail book={this.state.books.filter((book) => book.id === parseInt(match.params.bookId,10))[0]}
                    comments={this.state.comments.filter((comment) => comment.bookId === parseInt(match.params.bookId,10))}
                />
            );
        };
        const AdminBookWithId = ({match}) =>{
            return (
                <AdminBookDetail book={this.state.books.filter((book) => book.id === parseInt(match.params.bookId,10))[0]}
                                 changeInventory={this.changeInventory}
                                 changePrice={this.changePrice}
                />
            );
        };

        return (
            <div>
                <Header onLoginAccount = {this.state.onLoginAccount}/>
                <Switch>
                    <Route path="/home" component={HomePage} />
                    <PrivateRoute exact path="/menu" component={() => <Menu books={this.state.books}/>}/>
                    <PrivateRoute path="/menu/:bookId" component={BookWithId} />
                    <PrivateRoute path="/adminmenu/:bookId" component={AdminBookWithId} />
                    <Route exact path="/contactus" component={Contact} />
                    <Route exact path="/user/signup" component={() => <Signup accounts={this.state.accounts}/>}/>
                    <Route exact path="/user/login" component={() => <Login logined={sessionStorage.getItem("username") != null}/>}/>
                    <PrivateRoute exact path="/cart" component={() => <Cart books = {this.state.books}/>}/>
                    <PrivateRoute exact path="/order" component={() => <Order books = {this.state.books}/>}/>
                    <Route exact path="/aboutus" component={() => <About leaders={this.state.leaders}/>}/>
                    <PrivateRoute exact path="/admin/menu" component={() => <AdminMenu books={this.state.books}/>}/>
                    <PrivateRoute exact path="/admin/addbook" component={() => <AdminAddBook/>}/>
                    <PrivateRoute exact path="/admin/query" component={AdminQuery}/>
                    <PrivateRoute exact path="/admin/user" component={AdminUserBrowser}/>
                    <Route exact path="/chater" component={Chater}/>
                    <Redirect to="/home" />
                </Switch>
                <Demo/>
                <Footer/>
            </div>
        );
    }
}

export default Main;
