import React, { Component } from "react";
import {Card, CardBody, CardImg, CardText, CardTitle, Media} from "reactstrap";
import {Link} from "react-router-dom";
import {COMMENTS} from "../../shared/comments";
import {PROMOTIONS} from "../../shared/promotions";
import {LEADERS} from "../../shared/leaders";
import {ACCOUNTS} from "../../shared/accounts";
import Calendar from "../utils/Calendar";
import moment from "moment";
import {IP} from "../../shared/website";





class Order extends Component{
    constructor(props) {
        super(props);
        this.state = {
            orders: this.props.orders,
            books: this.props.books,
            date: moment(),
            startDate: moment().subtract(5, 'day'),
            endDate: moment().add(3, 'day')
        };
        this.setState({orders:this.props.orders});
        this.RenderOrder = this.RenderOrder.bind(this);
        this.order_get = this.order_get.bind(this);
        this.setDate = this.setDate.bind(this);
        if (sessionStorage.getItem("user_id") !== null) {
            this.order_get(this.state.startDate, this.state.endDate).then(r => {});
        }


    }

    order_get(startDate, endDate) {
        // data.createtime username payment
        return new Promise((resolve => {
            let formData = new URLSearchParams();
            formData.append('startDate', startDate);
            formData.append('endDate', endDate);
            let url = 'http://' + IP +'/order/' + sessionStorage.getItem("user_id");
            fetch(url,{
                method:'POST',
                body:formData
            })
                .then(res =>res.json())
                .then((data) => {
                    if (data === undefined) return;
                    for (let i = 0; i < data.length; i++) {
                        data[i].createtime = moment(data[i].createtime).format("YYYY-MM-DD HH:mm:ss"); //Format(yyyy-MM-dd HH:mm:ss)
                    }
                    console.log(data);
                    this.setState({ orders: data });
                })

        }));
    }

    RenderOrder() {
        if (this.state.orders == null || this.state.orders.length === 0) {
            return (
                <div key = "order_div" className="col-12 col-md-12">
                    <Card>
                        <h1></h1>
                    </Card>
                </div>
            );
        }
        console.log("orders", this.state.orders);
        if (this.state.orders === null || this.state.orders === undefined) {
            return (
                <div></div>
            );
        }
        console.log("orders", this.state.orders);
        let renderedOrders = this.state.orders.map((order, index) => {
            let renderedItems = order.orderItemSet.map((item, index) => {
                let book;
                if (item.book === null) {
                    book = {
                        name:""
                    };
                }
                else {
                    let item_id = item.book.id;
                    book = this.state.books.filter((book) => (book.id === item_id))[0];
                }
                if (book === undefined)
                    return (<div></div>);

                return (
                    <Card>
                        <Media>
                            <div className="col-12 col-md-2">
                                <CardImg width="100%" object src={book.image} alt={book.name}/>
                            </div>
                            <div className="col-12 col-md-7 m-2">
                                <Media body>
                                    <Media>{book.name}</Media>
                                    <Media>{item.itemprice} {item.itemcnt}</Media>
                                    <Media>{item.discountmoney}</Media>
                                    <Media>{item.totalmoney}</Media>
                                </Media>
                            </div>
                        </Media>
                    </Card>

                );
            });
            console.log("order_items = ", renderedItems);
            let date = new Date(order.createtime);
            // 
            let dateTime = date.toLocaleString();
            return (
                <div key = {index} className="col-12 col-md-12">
                    <Card>
                        <Media>
                            <div className="col-12 col-md-7 m-2">
                                <Media body>
                                    <Media heading>{order.orderid}   {order.payment}</Media>
                                    <Media >{dateTime}</Media>
                                </Media>
                                {renderedItems}
                            </div>
                        </Media>
                    </Card>
                </div>
            );
        });

        return (
            <React.Fragment>
                {renderedOrders}
            </React.Fragment>
        );
    }

    async setDate(startDate, endDate, mode) {
        console.log("mode = ", mode);
        const result = await this.order_get(startDate, endDate);
    }

    render() {
        let orders = this.RenderOrder();
        return (
            <React.Fragment>
                <div className="container">
                    <div className="row">
                        <div className="col-12 col-md-3">
                            <Calendar setDate = {this.setDate}/>
                        </div>
                        <div className="col-12 col-md-9">
                            {orders}
                        </div>
                    </div>
                </div>
            </React.Fragment>
        );
    }


}

export default Order;
