/**
 * [socketOpen|socketClose|socketMessage|socketError] = func[socket|||]
 * timeout
 * @type {module.webSocket}
 */
module.exports =  class webSocket {
    constructor(param = {}) {
        this.param = param;
        this.reconnectCount = 0;
        this.socket = null;
        this.taskRemindInterval = null;
        this.isSucces=true;
    }
    connection = () => {
        let {socketUrl, timeout = 0} = this.param;
        // socket
        if ('WebSocket' in window) {
            console.log('WebSocket');

            this.socket = new WebSocket(socketUrl);
        }
        else if ('MozWebSocket' in window) {
            console.log('MozWebSocket');

            this.socket = new MozWebSocket(socketUrl);
        }
        else {
            console.log('SockJS');

            this.socket = new SockJS(socketUrl);
        }
        console.log("socketUrl" + socketUrl);
        this.socket = new SockJS(socketUrl);
        this.socket.onopen = this.onopen;
        this.socket.onmessage = this.onmessage;
        this.socket.onclose = this.onclose;
        this.socket.onerror = this.onerror;
        this.socket.sendMessage = this.sendMessage;
        this.socket.closeSocket = this.closeSocket;
    };
    // 
    onopen = () => {
        let {socketOpen} = this.param;
        this.isSucces=false;  //false
        socketOpen && socketOpen();
    };
    // 
    onmessage = (msg) => {
        let {socketMessage} = this.param;
        socketMessage && socketMessage(msg);
    };
    // 
    onclose = (e) => {
        this.isSucces = true;   //true
        this.socket.close();
        let {socketClose} = this.param;
        socketClose && socketClose(e);
    };
    onerror = (e) => {
        // socket
        let {socketError} = this.param;
        this.socket = null;
        socketError && socketError(e);
    };
    sendMessage = (value) => {
        // 
        if(this.socket) {
            this.socket.send(JSON.stringify(value));
        }
    };
    closeSocket = () => {
        this.socket.close();
    };
    //
    readyState = () => {
        return this.socket.readyState;
    }
};
