import React, {Component} from 'react';
import {
    Card,
    CardImg,
    CardImgOverlay,
    CardText,
    CardBody,
    CardTitle,
    Breadcrumb,
    BreadcrumbItem,
    Form, FormGroup, Label, Col, Input, Button
} from 'reactstrap'
import { Link } from 'react-router-dom'
import {IP} from "../../shared/website";



class RenderAdminBook extends  Component {
    constructor(props) {
        super(props);
        let book = props.book;
        this.state={
            inventory:book.inventory,
            price:book.price,
            isbn:book.isbn,
            author:book.author,
            description:"",
            book:book
        };
        this.handleClick=this.handleClick.bind(this);
        this.handleInputChange=this.handleInputChange.bind(this);
    }

    handleInputChange(event) {
        event.preventDefault();
        const target = event.target;
        const value = target.value;
        const name = target.name;
        this.setState({
            [name]: value
        });
    }

    handleClick(event){
        const target = event.target;
        const id = target.id;
        const value = target.value;
        let params = new URLSearchParams();
        if (id === "delete") {
            let url = 'http://' + IP + '/book/' + this.props.book.id + '/delete';
            fetch(url,{
                method:'GET',
            })
                .then(res =>res.json())
                .then((data) => {
                    console.log(data);
                    if (data === null || data === undefined) return;
                    alert("");
                    this.setState({
                        book:data
                    })
                })
            return;
        }

        else if (id === 'inventory') params.append(id, this.state.inventory);
        else if (id === 'price') params.append(id, this.state.price);
        else if (id === 'isbn') params.append(id, this.state.isbn);
        else if (id === 'author') params.append(id, this.state.author);
        else if (id === 'description') params.append(id, this.state.description);
        let url = 'http://' + IP + '/book/' + this.props.book.id + '/' + id;
        fetch(url,{
            method:'POST',
            body:params
        })
            .then(res =>res.json())
            .then((data) => {
                console.log(data);
                if (data === null || data === undefined) return;
                alert("");
                this.setState({
                    book:data
                })
            })
    }

    EditBlock(name, value, bind_name) {
        return (
            <React.Fragment>
                <CardTitle >{name}{value}</CardTitle>
                <FormGroup row>
                    <Col md={{size:8, offset:2}}>
                        <Input type="text" name={bind_name}
                               placeholder={name}
                               onChange={this.handleInputChange}
                        />
                    </Col>
                    <Col md={{size:2}}>
                        <Button type="submit" color="primary" id={bind_name} onClick={this.handleClick}>
                            
                        </Button>
                    </Col>
                </FormGroup>
            </React.Fragment>
        );
    }

    render() {
        let inventory = this.EditBlock("", this.state.book.inventory, "inventory");
        let price = this.EditBlock("", this.state.book.price, "price");
        let ISBN = this.EditBlock("ISBN", this.state.book.isbn, "isbn");
        let description = this.EditBlock("", this.state.book.description, "description");
        return (
            <React.Fragment>
                <div key = "bookimage" className="col-12 col-md-5 m-1">
                    <Card>

                        <CardImg width="100%" object src={this.props.book.image} alt={this.props.book.name} />
                    </Card>
                </div>
                <div key = "bookdescription" className="col-12 col-md-5 m-1">
                    <h1>{this.props.book.name}</h1>
                    <br/><br/><br/>
                    <Card>
                        <CardBody>
                            <div className="row">
                                <Form>
                                    {inventory}
                                    {price}
                                    {ISBN}
                                    {description}
                                </Form>
                                <Button type="submit" color="primary" id="delete" onClick={this.handleClick}>
                                    
                                </Button>
                            </div>
                        </CardBody>
                    </Card>

                </div>
            </React.Fragment>
        );
    }


}

class AdminBookDetail extends Component{

    constructor(props) {
        super(props);
    }



    render() {
        if (this.props.book === null || this.props.book === undefined) return (<div>404</div>);
        else {
            return (
                <div className="container">
                    <div className="row">
                        <Breadcrumb>
                            <BreadcrumbItem><Link to='/adminmenu'></Link></BreadcrumbItem>
                            <BreadcrumbItem active>{this.props.book.name}</BreadcrumbItem>
                        </Breadcrumb>
                        <div className="col-12">
                            <h3>{this.props.book.name}</h3>
                            <hr />
                        </div>
                    </div>
                    <div className="row">
                        <RenderAdminBook
                            book={this.props.book}
                            changeInventory={this.props.changeInventory}
                            changePrice={this.props.changePrice}
                        />
                    </div>
                </div>
            );
        }
    }
}


export default AdminBookDetail;
