import LineChartComponent from "../utils/EchartsTest";
import {chartData} from "../../shared/data"
import React, {Component} from "react"
import Calendar from "../utils/Calendar";
import {Card, CardHeader, Media} from "reactstrap";
import moment from "moment";
import DenseTable from "../utils/Table";
import {IP} from "../../shared/website";


class AdminQuery extends Component{

    constructor(props) {
        super(props);
        this.state = {
            date: moment(),
            startDate: moment().subtract(5, 'day'),
            endDate: moment().add(3, 'day')
        };
        this.setDate = this.setDate.bind(this);
        this.order_get = this.order_get.bind(this);
    }

    async setDate(startDate, endDate, mode) {
        console.log("mode = ", mode);
        if (mode === 0) {
            const result = await this.order_get(startDate, endDate);
        }
        else if (mode === 1) {
            const result = await this.book_get(startDate, endDate);
        }
    }

    order_get(startDate, endDate) {
        // data.createtime username payment
        return new Promise((resolve => {
            let formData = new URLSearchParams();
            formData.append('startDate', startDate);
            formData.append('endDate', endDate);
            fetch('http://' + IP + '/statics/order',{
                method:'POST',
                body:formData
            })
                .then(res =>res.json())
                .then((data) => {
                    if (data === undefined) return;
                    for (let i = 0; i < data.length; i++) {
                        data[i].createtime = moment(data[i].createtime).format("YYYY-MM-DD HH:mm:ss"); //Format(yyyy-MM-dd HH:mm:ss)
                    }
                    let result_array = [];
                    for (let i = 0; i < data.length; i++) {
                        let found = -1;
                        for (let j = 0; j < result_array.length; j++) {
                            if (result_array[j].createtime === data[i].createtime && result_array[j].username === data[i].username) {
                                found = j;
                                break;
                            }
                        }
                        if (found === -1) {
                            result_array.push(data[i]);
                        }
                        else {
                            result_array[found].payment += data[i].payment;
                        }
                    }

                    this.setState({ orders: result_array });
                })

        }));
    }

    book_get(startDate, endDate) {
        return new Promise((resolve => {
            let formData = new URLSearchParams();
            formData.append('startDate', startDate);
            formData.append('endDate', endDate);
            fetch('http://' + IP + '/statics/book',{
                method:'POST',
                body:formData
            })
                //createtime=2021-06-02 22:04:20.0, book=(), count=1
                .then(res =>res.json())
                .then((data) => {
                    console.log(data)
                    if (data === undefined) return;

                    for (let i = 0; i < data.length; i++) {
                        data[i].createtime = moment(data[i].createtime).format("YYYY-MM-DD HH:mm:ss"); //HH:mm:ss
                    }

                    let result_array = [];
                    for (let i = 0; i < data.length; i++) {
                        let found = -1;
                        for (let j = 0; j < result_array.length; j++) {
                            if (result_array[j].createtime === data[i].createtime && result_array[j].book === data[i].book) {
                                found = j;
                                break;
                            }
                        }
                        if (found === -1) {
                            result_array.push(data[i]);
                        }
                        else {
                            result_array[found].count += data[i].count;
                        }
                    }
                    console.log("resultarray = ", result_array);
                    this.setState({ books: result_array });
                })

        }));
    }

    render() {
        return (
            <div className="container">
                <Card>
                        <CardHeader><h3></h3></CardHeader>
                        <Media body>
                            <div className="row">
                                <div key = "calender" className="col-12 col-md-4 ">
                                    <Calendar setDate = {this.setDate}  mode={0}/>
                                </div>
                                <div className="col-12 col-md-7">
                                    <LineChartComponent startDate = {this.state.startDate} endDate = {this.state.endDate} data = {this.state.orders} color={"username"} position={"createtime*payment"}/>
                                </div>
                            </div>
                        </Media>
                    <DenseTable data={this.state.orders} title1={""} title2={""} title3={""}/>
                </Card>
                <Card>
                    <CardHeader><h3></h3></CardHeader>
                    <Media body>
                        <div className="row">
                            <div key = "calender" className="col-12 col-md-4 ">
                                <Calendar setDate = {this.setDate}  mode={1}/>
                            </div>
                            <div className="col-12 col-md-7">
                                <LineChartComponent startDate = {this.state.startDate} endDate = {this.state.endDate} data = {this.state.books} color={"book"} position={"createtime*count"}/>
                            </div>
                        </div>
                    </Media>
                    <DenseTable data={this.state.books}  title1={""} title2={""} title3={""}/>
                </Card>

            </div>
        );
    }
}

export default  AdminQuery;
